#main-footer
  background-color: $background-dark
  color: #fff
  padding-top: 100px
  .main-footer__container
    svg
      height: 70px
      display: block
      margin-bottom: 80px
    .widget_nav_menu
      margin-bottom: 80px
      .menu
        list-style: none
        padding: 0
        margin: 0
        li
          a
            text-decoration: none
            color: #fff
            font-size: 17px
            line-height: 42px
            position: relative
            &:hover
              &:before
                visibility: visible
                width: 100%
            &:before
              content: ''
              position: absolute
              width: 0
              height: 2px
              bottom: -5px
              left: 0
              background: $accent
              background: linear-gradient(90deg, $accent 0%, $primary-color 100%)
              visibility: hidden
              transition: all 0.3s ease-in-out
    .widget-title
      font-size: 22px
      line-height: 32px
      letter-spacing: -0.66px
      margin-bottom: 20px

    .footer-contact-details
      margin-bottom: 80px
      p
        font-size: 17px
        line-height: 30px
        letter-spacing: 0px
        margin: 0
      ul
        list-style: none
        padding: 0
        margin: 0
        li
          font-size: 17px
          line-height: 30px
          letter-spacing: 0px
          a
            text-decoration: none
            color: #fff
            position: relative
            &:hover
              &:before
                visibility: visible
                width: 100%
            &:before
              content: ''
              position: absolute
              width: 0
              height: 2px
              bottom: -5px
              left: 0
              background: $accent
              background: linear-gradient(90deg, $accent 0%, $primary-color 100%)
              visibility: hidden
              transition: all 0.3s ease-in-out

  .footer-nav-container
    border-top: 1px solid rgb(255 255 255 / 20%)
    padding: 40px 0px
    .footer-nav-content
      display: flex
      justify-content: space-between
      p
        margin: 0
        a
          text-decoration: none
          color: #4DEBB3
    .footer-nav-wrapper
      .footer-nav
        list-style: none
        padding: 0px
        margin: 0px
        display: flex
        li
          &:last-of-type
            a
              margin-right: 0px
          a
            display: inline-block
            margin-right: 40px
            text-decoration: none
            color: #fff
            font-size: 17px
            position: relative
            &:hover
              &:before
                visibility: visible
                width: 100%
            &:before
              content: ''
              position: absolute
              width: 0
              height: 2px
              bottom: -5px
              left: 0
              background: $accent
              background: linear-gradient(90deg, $accent 0%, $primary-color 100%)
              visibility: hidden
              transition: all 0.3s ease-in-out

@include media-breakpoint-down(lg)
  #main-footer
    .main-footer__container
      .footer-logo-button
        display: flex
        justify-content: space-between
        align-items: center
        margin-bottom: 80px
      .btn--demo-request
        display: inline-block
        margin-bottom: 0px
      svg
        display: inline-block
        margin-bottom: 0px

    .footer-nav-container
      .footer-nav-wrapper
        justify-content: flex-start

@include media-breakpoint-down(sm)
  #main-footer
    .main-footer__container
      .footer-logo-button
        flex-direction: column
        justify-content: flex-start
        align-items: flex-start
        margin-bottom: 40px
      svg
        margin-bottom: 40px
      .widget_nav_menu
        margin-bottom: 40px
      .footer-contact-details
        .footer-contacts-info
          margin-top: 20px
    .footer-nav-container
      .footer-nav-content
        flex-direction: column
      .footer-nav-wrapper
        margin-bottom: 30px
        .footer-nav
          flex-direction: column